//FOOTER
footer {
  background-color: $blue_color;
  padding-top: 1rem;

  .MuiContainer-root {
    .MuiBox-root {
      .MuiGrid-root {
        padding-top: 2rem !important;

        @include responsive(lg) {
          text-align: center;
        }

        //COLUMN ONE
        .footer-one {
          .btn-home {
            border: none;
            color: $blue_color;
            box-shadow: none !important;

            &:hover,
            &:focus {
              background-color: $blue_color;
              border: none !important;
              box-shadow: none !important;
            }

            .logo {
              width: 250px;

              @include responsive(sm) {
                width: 200px;
              }
            }
          }
        }

        //COLUMN TWO AND THREE
        .footer-two,
        .footer-three,
        .footer-four,
        .footer-five {
          display: flex;
          text-align: center;
          flex-direction: column;

          .MuiButton-text {
            border: none;
            color: $blue_color;
            box-shadow: none !important;

            &:hover,
            &:focus {
              background-color: $blue_color;
              border: none !important;
              box-shadow: none !important;
            }

            svg {
              color: white;
              padding-top: 9px;
            }

            a {
              color: $white;
              text-transform: capitalize !important;
              font-family: $cera_gr;
              font-size: 18px;
              text-decoration: none;
              border: none;
              box-shadow: none !important;
              padding-top: 0.8rem;

              @include responsive(md) {
                padding-top: 0.4rem;
              }

              &:hover,
              &:focus,
              &:active {
                color: $green_color;
                box-shadow: none !important;
                background-color: transparent !important;
              }
            }
          }
        }

        .footer-six {
          text-align: center;

          .copyright {
            padding-top: 1rem;
            padding-bottom: 0.4rem;
            color: $gray;
            font-size: 13px;
            font-weight: 100;
          }
        }

        .MuiPaper-root {
          font-size: 18.5px;
          color: $white;
          box-shadow: none !important;
          font-family: $cera_gr_bold;
          background-color: $blue_color !important;

          @include responsive(md) {
            padding: 3px;
          }
        }
      }
    }
  }
}
