//WHY
.why {
  .main-wrapper {
    position: relative;

    .container-one {
      width: 100%;
      height: 80vw;
      overflow: hidden;
      position: relative;

      .circle {
        background-color: $blue_color;
        width: 200vw;
        line-height: 200vw;
        overflow: hidden;
        border-radius: 100%;
        position: absolute;
        bottom: 28%;
        top: -100%;
        transform: translateX(-52%);
        margin: 0px 28%;
      }

      .image {
        display: inline-block;
        width: 100%;
        height: 30vw;
        background-color: transparent;
        vertical-align: bottom;
        background-size: cover;
        background-position: center 40%;
      }

      @include responsive(lg) {
        display: none;
      }
    }

    .container-two {
      background-color: $blue_color;
      color: $white;
      position: absolute;
      top: 45%;
      left: 50%;
      right: -30%;
      transform: translate(-50%, -50%);
      color: $white;
      background-color: transparent;

      @include responsive(lg) {
        background-color: $blue_color;
        color: $white;
        top: 0;
        left: 0;
        transform: translate(0, 0);
        position: inherit;
        padding-top: 1rem;
        padding-bottom: 1rem;
      }

      .MuiGrid-root {
        .MuiGrid-item {
          text-align: center;

          .title {
            font-size: 55px;
            font-family: $cera_gr_bold;
            padding-top: 2rem;
            color: $white;

            @include responsive(lg) {
              font-size: 45px;
            }

            @include responsive(sm) {
              font-size: 30px;
            }
          }

          .text {
            font-family: $cera_gr;
            padding-top: 1rem;
            padding-bottom: 1rem;
            color: $white;

            @include responsive(sm) {
              font-size: 14px;
            }
          }

          img {
            width: 100%;
            border-radius: 10px;
          }
        }
      }
    }
  }

  .why-container {
    padding-bottom: 5rem;

    @include responsive(lg) {
      padding-top: 2.5rem;
      padding-bottom: 2.5rem;
    }

    .MuiGrid-container {
      .MuiGrid-item {
        width: 100%;
        text-align: center;
        .main-title {
          font-size: 40px;
          font-family: $cera_gr_bold;
          padding-bottom: 1rem;
          color: $blue_color;

          @include responsive(md) {
            font-size: 32px;
          }

          @include responsive(sm) {
            font-size: 23px;
          }
        }

        .MuiCard-root {
          box-shadow: none;
          border: 3px solid $light;
          border-radius: 6px;
          width: 100%;
          height: 100%;

          .img-wrap {
            padding-top: 20px;
            img {
              width: 30%;
              padding-left: 30px;

              @include responsive(sm) {
                width: 25%;
              }
            }
          }

          .MuiCardContent-root {
            .MuiTypography-root {
              color: $blue_color;
              font-family: $cera_gr_bold;
              font-size: 20px;

              @include responsive(lg) {
                font-size: 19px;
              }

              @include responsive(md) {
                font-size: 18px;
              }
            }
          }
        }
      }
    }
  }
}
