//HOME
.home {
  .main-wrapper {
    position: relative;
    background: $light;

    .container-one {
      width: 100%;
      height: 65vw;
      overflow: hidden;
      position: relative;

      .circle {
        background-color: $blue_color;
        width: 200vw;
        line-height: 200vw;
        overflow: hidden;
        border-radius: 100%;
        position: absolute;
        bottom: 25%;
        top: -100%;
        transform: translateX(-52%);
        margin: 0px 28%;
      }

      .image {
        display: inline-block;
        width: 100%;
        height: 30vw;
        background-color: transparent;
        vertical-align: bottom;
        background-size: cover;
        background-position: center 40%;
      }

      @include responsive(lg) {
        display: none;
      }
    }

    .container-two {
      background-color: $blue_color;
      color: white;
      position: absolute;
      top: 50%;
      left: 50%;
      right: -30%;
      transform: translate(-50%, -50%);
      color: white;
      background-color: transparent;

      @include responsive(lg) {
        background-color: $blue_color;
        color: white;
        top: 0;
        left: 0;
        transform: translate(0, 0);
        position: inherit;
        padding-top: 1rem;
        padding-bottom: 1rem;
      }

      .MuiGrid-root {
        .MuiGrid-item {
          text-align: center;

          .title {
            font-size: 55px;
            font-family: $cera_gr_bold;
            padding-top: 2rem;
            color: white;

            @include responsive(lg) {
              font-size: 45px;
            }

            @include responsive(sm) {
              font-size: 30px;
            }
          }

          .text {
            font-family: $cera_gr;
            padding-top: 2rem;
            padding-bottom: 2rem;
            color: white;

            @include responsive(sm) {
              font-size: 14px;
            }
          }

          .wrapper {
            padding-bottom: 2rem;

            a {
              text-decoration: none;
            }
            .btn-sign-up {
              padding-top: 0.55rem;
              padding-bottom: 0.35rem;
              padding-left: 2.25rem;
              padding-right: 2.25rem;
              background-color: $green_color;
              border: 2px solid $green_color;
              color: $blue_color;
              font-family: $cera_gr_bold;
              border-radius: 4px;
              font-size: 17px;
              font-weight: 600;

              &:hover {
                color: $green_color;
                background-color: $blue_color;
                transform: 0.9s ease-in;
              }
            }
          }

          img {
            width: 100%;
            border-radius: 8px;
          }
        }
      }
    }
  }

  .service-container {
    padding-bottom: 5rem;
    background-color: $light;
    padding-top: 1rem;

    @include responsive(lg) {
      padding-top: 2rem;
      padding-bottom: 2rem;
    }

    max-width: 2500px !important;

    .MuiGrid-container {
      .MuiGrid-item {
        width: 100%;
        text-align: center;

        .main-title {
          font-size: 35px;
          font-family: $cera_gr_bold;
          color: $blue_color;
          padding-bottom: 1rem;

          @include responsive(md) {
            font-size: 32px;
          }

          @include responsive(sm) {
            font-size: 22px;
          }
        }

        .MuiCard-root {
          box-shadow: none;
          text-align: center;
          padding-top: 4rem;
          width: 100%;
          height: 100%;
          background-color: $light;

          @include responsive(sm) {
            padding-top: 1rem;
          }

          .img-wrap {
            text-align: -webkit-center;

            img {
              width: 25%;
            }
          }

          .MuiCardContent-root {
            .MuiTypography-root {
              color: $dark;
              font-family: $cera_gr_bold;
              font-size: 19px;

              @include responsive(lg) {
                font-size: 17.5px;
              }

              @include responsive(md) {
                font-size: 15px;
              }
            }
          }
        }
      }
    }
  }
}
