//FAQ
.faq {
  .main-wrapper {
    position: relative;

    .container-one {
      width: 100%;
      height: 50vw;
      overflow: hidden;
      position: relative;

      .circle {
        background-color: $blue_color;
        width: 200vw;
        line-height: 200vw;
        overflow: hidden;
        border-radius: 100%;
        position: absolute;
        bottom: 30%;
        top: -100%;
        transform: translateX(-40%);
        margin: 0px 28%;
      }

      .image {
        display: inline-block;
        width: 100%;
        height: 30vw;
        background-color: transparent;
        vertical-align: bottom;
        background-size: cover;
        background-position: center 40%;
      }

      @include responsive(lg) {
        display: none;
      }
    }

    .container-two {
      background-color: $blue_color;
      color: white;
      position: absolute;
      top: 50%;
      left: 50%;
      right: -30%;
      transform: translate(-50%, -50%);
      color: white;
      background-color: transparent;

      @include responsive(lg) {
        background-color: $blue_color;
        color: white;
        top: 0;
        left: 0;
        transform: translate(0, 0);
        position: inherit;
        padding-top: 1rem;
        padding-bottom: 1rem;
      }

      .MuiGrid-root {
        .MuiGrid-item {
          text-align: center;

          .title {
            font-size: 55px;
            font-family: $cera_gr_bold;
            padding-top: 2rem;
            color: white;

            @include responsive(lg) {
              font-size: 45px;
            }

            @include responsive(sm) {
              font-size: 30px;
            }
          }

          .text {
            font-family: $cera_gr;
            padding-top: 1rem;
            padding-bottom: 1rem;
            color: white;

            @include responsive(sm) {
              font-size: 14px;
            }
          }

          .wrapper {
            .MuiButton-root {
              display: none;
            }
          }

          img {
            width: 100%;
            border-radius: 8px;
          }
        }
      }
    }
  }

  .faq-container {
    padding-bottom: 5rem;

    @include responsive(lg) {
      padding-top: 2.5rem;
      padding-bottom: 2.5rem;
    }

    .MuiGrid-container {
      .MuiGrid-grid-xs-12 {
        border-bottom: 3px solid $light;
        padding-bottom: 8px;

        .MuiPaper-root {
          box-shadow: none;

          .main-title {
            font-size: 40px;
            font-family: $cera_gr_bold;
            padding-bottom: 1rem;

            @include responsive(md) {
              font-size: 30px;
            }

            @include responsive(sm) {
              font-size: 21px;
            }
          }

          .MuiAccordionSummary-root {
            .question {
              font-size: 20px;
              font-family: $cera_gr_bold;

              @include responsive(sm) {
                font-size: 14.5px;
              }
            }

            svg {
              color: $blue_color;
            }
          }

          .MuiAccordionDetails-root {
            .answer {
              color: $gray;
              font-family: $cera_gr;
              font-size: 17px;

              @include responsive(sm) {
                font-size: 13.5px;
              }
            }
          }
        }
      }
    }
  }
}
