@mixin responsive($breakpoint) {
  @if $breakpoint==xs {
    @media (max-width: 0px) {
      @content;
    }
  }

  @if $breakpoint==sm {
    @media (max-width: 599px) {
      @content;
    }
  }

  @if $breakpoint==md {
    @media (max-width: 899px) {
      @content;
    }
  }

  @if $breakpoint==lg {
    @media (max-width: 1199px) {
      @content;
    }
  }

  @if $breakpoint==xl {
    @media (max-width: 1536px) {
      @content;
    }
  }

  @if $breakpoint==xxl {
    @media (max-width: 2000px) {
      @content;
    }
  }
}
