//Apply
.apply {
  .main-wrapper {
    position: relative;

    .container-one {
      width: 100%;
      height: 35vw;
      overflow: hidden;
      position: relative;

      .circle {
        z-index: -1;
        background-color: $blue_color;
        width: 200vw;
        line-height: 200vw;
        overflow: hidden;
        border-radius: 100%;
        position: absolute;
        bottom: 5%;
        top: -100%;
        transform: translateX(-25%);
        margin: 0px 28%;
      }

      .image {
        display: inline-block;
        width: 100%;
        height: 30vw;
        background-color: transparent;
        vertical-align: bottom;
        background-size: cover;
        background-position: center 40%;
      }

      @include responsive(lg) {
        display: none;
      }
    }

    .container-two {
      background-color: $blue_color;
      color: white;
      position: absolute;
      top: 45%;
      left: 50%;
      right: -30%;
      transform: translate(-50%, -50%);
      color: white;
      background-color: transparent;

      @include responsive(lg) {
        background-color: $blue_color;
        color: white;
        top: 0;
        left: 0;
        transform: translate(0, 0);
        position: inherit;
        padding-top: 1rem;
      }

      .MuiGrid-root {
        .MuiGrid-item {
          text-align: center;

          .title {
            font-size: 55px;
            font-family: $cera_gr_bold;
            padding-top: 2rem;
            color: white;

            @include responsive(lg) {
              font-size: 45px;
            }

            @include responsive(sm) {
              font-size: 30px;
            }
          }

          .text {
            font-family: $cera_gr;
            padding-top: 1rem;
            padding-bottom: 1rem;
            color: white;

            @include responsive(sm) {
              font-size: 14px;
            }
          }

          .wrapper {
            .MuiButton-root {
              display: none;
            }
          }

          img {
            display: none;
          }
        }
      }
    }
  }

  .MuiContainer-root {
    padding-bottom: 3rem;
    padding-right: 32px;

    @include responsive(lg) {
      padding-bottom: 1rem;
    }

    .item {
      margin-top: -30%;
      border-radius: 8px;
      box-shadow: 1px 1px 8px 8px $light;

      @include responsive(lg) {
        margin-top: 3%;
        box-shadow: none;
        padding-left: 1px;
        padding-right: 1px;
      }

      .MuiGrid-root {
        .main-col1 {
          .MuiGrid-grid-lg-2 {
            @include responsive(lg) {
              display: none;
            }
          }

          @include responsive(lg) {
            order: 1;
            padding-top: 1rem;
            padding-bottom: 1rem;
          }

          .upload-col {
            .wrapper {
              background-color: $light;
              padding-top: 2rem;
              padding-bottom: 2rem;
              border-radius: 8px;

              .MuiTypography-root {
                padding-left: 20px;
                font-family: $cera_gr_bold;
              }

              .MuiButton-root {
                background-color: transparent;
                color: $purple_light;
                text-transform: capitalize;
                font-size: 14px;
                font-family: $cera_gr_bold;
                box-shadow: none;

                .MuiSvgIcon-root {
                  border: 2px solid;
                  border-radius: 8px;
                }
              }
            }
          }

          .safe-hands {
            font-family: $cera_gr_bold;
            font-size: 40px;

            @include responsive(md) {
              font-size: 35px;
            }

            @include responsive(sm) {
              font-size: 24px;
            }
          }

          .note {
            padding-top: 1rem;
            font-family: $cera_gr;

            @include responsive(sm) {
              font-size: 11px;
            }
          }

          .MuiToggleButtonGroup-root {
            padding-top: 3.2rem;
            width: 100%;

            @include responsive(md) {
              padding-top: 2rem;
            }

            .MuiButtonBase-root[aria-pressed="true"] {
              color: $white;
              background-color: $blue_color;
            }

            .MuiButtonBase-root {
              width: 30%;
              padding: 25px;
              font-size: 20px;
              border: none;
              font-family: $cera_gr_bold;
              background-color: $light;
              color: $blue_color;

              @include responsive(md) {
                width: 50%;
                padding: 15px;
                font-size: 15px;
              }
            }
          }

          .MuiGrid-container {
            .btn-send {
              padding-top: 0.55rem;
              padding-left: 1.2rem;
              padding-right: 1.2rem;
              background-color: $blue_color;
              border: 2px solid $blue_color;
              color: $white;
              font-family: $cera_gr_bold;
              border-radius: 6px;
              font-size: 20px;
              text-transform: capitalize;
              font-weight: 600;

              @include responsive(md) {
                padding-top: 0.35rem;
                padding-bottom: 0.45rem;
              }

              &:hover {
                color: $white;
                background-color: $green_color;
                border: 2px solid $green_color;
                transform: 0.9s ease-in;
              }
            }

            .label {
              padding-top: 0.75rem;
              padding-bottom: 0.75rem;
              font-family: $cera_gr;
              font-size: 15.5px;
              color: $gray;
              font-weight: 600;
            }

            input,
            textarea {
              resize: none;
              width: 100%;
              border-radius: 8px;
              padding-top: 1.2rem;
              padding-bottom: 1.2rem;
              font-family: $cera_gr;
              box-shadow: none !important;
              border: none !important;
              background-color: $light;
              outline: none;
              padding-left: 12px;

              &:hover,
              &:focus,
              &:after {
                border: none !important;
                box-shadow: none !important;
                background-color: $light;
                outline: none;
              }
            }
          }
        }

        .main-col2 {
          @include responsive(lg) {
            order: 0;
            padding-top: 1rem;
            padding-bottom: 1rem;
          }

          .contact-img {
            @include responsive(lg) {
              display: none;
            }

            width: 100%;
            height: 1200px;
          }
        }
      }
    }
  }
}
