//SERVICES
.services {
  .main-wrapper {
    position: relative;

    .container-one {
      width: 100%;
      height: 35vw;
      overflow: hidden;
      position: relative;
      background-color: $light;

      .circle {
        background-color: $blue_color;
        width: 200vw;
        line-height: 200vw;
        overflow: hidden;
        border-radius: 100%;
        position: absolute;
        bottom: 5%;
        top: -100%;
        transform: translateX(-40%);
        margin: 0px 28%;
      }

      .image {
        display: inline-block;
        width: 100%;
        height: 30vw;
        background-color: transparent;
        vertical-align: bottom;
        background-size: cover;
        background-position: center 40%;
      }

      @include responsive(lg) {
        display: none;
      }
    }

    .container-two {
      color: $white;
      position: absolute;
      top: 45%;
      left: 50%;
      right: -30%;
      transform: translate(-50%, -50%);
      background-color: transparent;

      @include responsive(lg) {
        background-color: $blue_color;
        color: $white;
        top: 0;
        left: 0;
        transform: translate(0, 0);
        position: inherit;
        padding-top: 1rem;
        padding-bottom: 1rem;
      }

      .MuiGrid-root {
        .MuiGrid-item {
          text-align: center;

          .MuiPaper-root {
            background-color: $blue_color !important;
            box-shadow: none;

            .title {
              font-size: 55px;
              font-family: $cera_gr_bold;
              padding-top: 2rem;
              color: $white;

              @include responsive(lg) {
                font-size: 45px;
              }

              @include responsive(sm) {
                font-size: 30px;
              }
            }

            .text {
              font-family: $cera_gr;
              padding-top: 1rem;
              padding-bottom: 1rem;
              color: $white;

              @include responsive(sm) {
                font-size: 14px;
              }
            }
          }
        }
      }
    }
  }

  .service-container {
    padding-bottom: 5rem;
    background-color: $light;
    padding-top: 1rem;

    @include responsive(lg) {
      padding-top: 2rem;
      padding-bottom: 2rem;
    }

    max-width: 2500px !important;

    .MuiGrid-container {
      .MuiGrid-item {
        width: 100%;
        text-align: center;

        .main-title {
          font-size: 35px;
          font-family: $cera_gr_bold;
          color: $blue_color;
          padding-bottom: 1rem;

          @include responsive(md) {
            font-size: 32px;
          }

          @include responsive(sm) {
            font-size: 22px;
          }
        }

        .MuiCard-root {
          box-shadow: none;
          text-align: center;
          padding-top: 4rem;
          width: 100%;
          height: 100%;
          background-color: $light;

          @include responsive(sm) {
            padding-top: 1rem;
          }

          .img-wrap {
            text-align: -webkit-center;

            img {
              width: 25%;
            }
          }

          .MuiCardContent-root {
            .MuiTypography-root {
              color: $dark;
              font-family: $cera_gr_bold;
              font-size: 19px;

              @include responsive(lg) {
                font-size: 17.5px;
              }

              @include responsive(md) {
                font-size: 15px;
              }
            }
          }
        }
      }
    }
  }

  .guidance-container {
    padding-bottom: 5rem;
    padding-top: 2rem;

    @include responsive(lg) {
      padding-top: 2rem;
      padding-bottom: 2rem;
    }
    .MuiGrid-container {
      justify-content: center;
      .MuiGrid-item {
        width: 100%;
        text-align: center;

        .main-title {
          font-size: 40px;
          font-family: $cera_gr_bold;
          color: $blue_color;
          padding-bottom: 1rem;

          @include responsive(md) {
            font-size: 32px;
          }

          @include responsive(sm) {
            font-size: 22px;
          }
        }

        .MuiCard-root {
          box-shadow: none;
          text-align: center;
          border-radius: 6px;
          border: 2px solid $light;
          width: 100%;
          height: 100%;

          @include responsive(sm) {
            padding-top: 0.5rem;
          }

          .img-wrap {
            padding-top: 2rem;
            text-align: -webkit-center;

            @include responsive(sm) {
              padding-top: 1rem;
            }

            img {
              width: 30%;
            }
          }

          .MuiCardContent-root {
            .MuiTypography-root {
              color: $blue_color;
              padding-bottom: 1rem;
              font-family: $cera_gr;
              font-size: 20px;

              @include responsive(lg) {
                font-size: 18px;
              }

              @include responsive(md) {
                font-size: 16px;
              }

              @include responsive(sm) {
                padding-bottom: 0;
                font-size: 14px;
              }
            }
          }

          &:hover {
            background-color: $green_color;
            color: $white !important;
            box-shadow: 0px 0px 2px 2px $green_color;
            border: 2px solid $green_color;
          }
        }
      }
    }
  }

  .carousel-container {
    padding-bottom: 5rem;
    padding-top: 2rem;

    @include responsive(lg) {
      padding-top: 2rem;
      padding-bottom: 2rem;
    }

    .MuiGrid-container {
      .MuiGrid-item {
        padding: 21px;

        .main-title {
          text-align: center;
          color: $blue_color;
          font-size: 42px;
          font-family: $cera_gr_bold;

          @include responsive(md) {
            font-size: 32px;
          }

          @include responsive(sm) {
            font-size: 22px;
          }
        }

        .main-text {
          text-align: center;
          color: $gray;
          font-family: $cera_gr;
          font-size: 20px;

          @include responsive(sm) {
            font-size: 13px;
          }
        }

        .MuiCard-root {
          margin: 20px;
          border-radius: 8px;
          box-shadow: 1px 1px 8px 8px $light;

          .MuiCardContent-root {
            .title {
              padding-bottom: 1rem;
              width: 80%;
              padding-top: 1rem;
              padding-left: 1rem;
              border: 2px solid $blue_color;
              font-family: $cera_gr_bold;
              color: $white;
              background-color: $blue_color;
              border-radius: 4px;
              font-size: 1.4rem;

              @include responsive(lg) {
                width: 90%;
                font-size: 1rem;
              }

              @include responsive(sm) {
                width: 90%;
                font-size: 0.75rem;
              }
            }

            p {
              color: $gray;
              font-family: $cera_gr;
              padding-top: 1.25rem;
              padding-bottom: 1.25rem;

              @include responsive(md) {
                font-size: 12.5px;
              }
            }

            a {
              text-decoration: none;
            }

            .btn-apply {
              padding-top: 0.7rem;
              padding-bottom: 0.55rem;
              padding-left: 2.2rem;
              padding-right: 2.2rem;
              background-color: $blue_color;
              border: none;
              color: $white;
              font-family: $cera_gr;
              border-radius: 6px;
              font-size: 15px;
              text-transform: capitalize;
              font-weight: 600;

              @include responsive(md) {
                padding-top: 0.35rem;
                padding-bottom: 0.45rem;
                padding-left: 1.6rem;
                padding-right: 1.6rem;
              }

              &:hover {
                background-color: $green_color;
                transform: 0.9s ease-in;
              }
            }
          }
        }

        .btn-next,
        .btn-previous {
          padding-top: 0.7rem;
          padding-bottom: 0.55rem;
          background-color: $blue_color;
          color: $white;
          font-family: $cera_gr_bold;
          border-radius: 6px;
          font-size: 16px;
          text-transform: capitalize;
          font-weight: 600;
        }

        .next-wrapper {
          text-align: end;
        }
      }
    }
  }
}
