//HEADER
header {
  .main-box {
    display: flex;

    .MuiPaper-root {
      background-color: $blue_color;
      color: $black;
      box-shadow: none;
      border: none;

      .MuiContainer-root {
        max-width: 1200px;
        .MuiToolbar-root {
          padding-left: 0;

          .menu-btn {
            @include responsive(xxl) {
              display: none !important;
            }

            @include responsive(md) {
              display: inline-block !important;
              flex-grow: 1;
              text-align: end;
              order: 1;
            }

            .MuiIconButton-root {
              &:hover {
                background-color: $blue_color;
              }

              .MuiSvgIcon-root {
                color: $white;
              }
            }
          }

          .btn-home {
            line-height: 0 !important;
            align-self: start;
            border: none;
            color: $blue_color;
            box-shadow: none !important;
            padding-top: 12px;

            @include responsive(md) {
              text-align: start;
              order: 0;
            }

            &:hover,
            &:focus {
              background-color: $blue_color;
              border: none !important;
              box-shadow: none !important;
            }
          }

          .item-box {
            flex-grow: 1;

            @include responsive(xl) {
              display: block;
              text-align: center;
            }

            @include responsive(md) {
              display: none !important;
            }

            .active {
              color: $green_color !important;
            }

            .MuiButton-text {
              border: none;
              color: $blue_color;
              box-shadow: none !important;
              font-weight: 600;

              &:hover,
              &:focus {
                background-color: $blue_color;
                border: none !important;
                box-shadow: none !important;
              }

              .nav-link {
                color: $white;
                text-transform: capitalize !important;
                font-size: 18px;
                font-family: $cera_gr;
                padding: 2px 16px;
                text-decoration: none;
                box-shadow: none !important;
                border: none;
                font-weight: 600;

                &:hover,
                &:focus {
                  color: $green_color !important;
                  background-color: $blue_color !important;
                  box-shadow: none !important;
                }

                @include responsive(lg) {
                  font-size: 15px;
                }
              }
            }
          }

          .MuiButton-outlined {
            padding-top: 0.55rem;
            padding-bottom: 0.35rem;
            padding-left: 2.2rem;
            padding-right: 2.2rem;
            border: 3px solid $green_color;
            font-size: 17px;
            color: $green_color;
            font-family: $cera_gr;
            font-weight: 600;

            @include responsive(xl) {
              display: block;
            }

            @include responsive(md) {
              display: none !important;
            }

            a {
              color: $green_color;
              text-decoration: none;
            }
          }
        }
      }
    }
  }
}

//DRAWER MODAL
.drawer {
  .MuiPaper-root {
    height: 100%;
    box-sizing: border-box;
    background-color: $blue_color;

    .MuiContainer-root {
      .main-box {
        width: 250px;
        .close-btn {
          padding-top: 1.5rem;
          text-align: end;

          svg {
            color: $white;
          }
        }
        .item-box {
          display: flex;
          flex-direction: column;

          .active {
            color: $green_color !important;
          }

          .MuiButton-text {
            align-self: start;
            border: none;
            color: $blue_color;
            box-shadow: none !important;
            font-weight: 600;

            &:hover,
            &:focus {
              background-color: $blue_color;
              border: none !important;
              box-shadow: none !important;
            }

            .nav-link {
              color: $white;
              text-transform: capitalize !important;
              font-family: $cera_gr;
              font-size: 18px;
              text-decoration: none;
              border: none;
              box-shadow: none !important;
              padding-top: 0.8rem;
              font-weight: 600;

              &:hover,
              &:focus {
                color: $green_color;
                box-shadow: none !important;
                background-color: transparent !important;
              }
            }
          }
        }

        .apply {
          padding-top: 1.5rem;

          .MuiButton-outlined {
            padding-top: 0.55rem;
            padding-bottom: 0.35rem;
            padding-left: 2.2rem;
            padding-right: 2.2rem;

            font-size: 17px;
            border: 3px solid $green_color;
            color: $green_color;
            width: 50%;
            font-family: $cera_gr;
            font-weight: 600;

            a {
              color: $green_color;
              text-decoration: none;
            }
          }
        }
      }
    }
  }
}
